.dishes__title {
  font-size: 24px;
  font-weight: 600;
  line-height: 32px;
  color: var(--black);
  margin-bottom: 32px;
}

.dishes__list {
  display: flex;
  justify-content: start;
  flex-wrap: wrap;
  column-gap: 32px;
  row-gap: 48px;
}

.dish-card {
  max-width: 384px;
}

.dish-card__image-container {
  position: relative;
  background-color: #f9f5ff;
  height: 240px;
  margin-bottom: 32px;
}

.dish-card__heart-icon {
  position: absolute;
  z-index: 50;
  top: 24px;
  right: 24px;
}

.dish-card__title {
  font-size: 24px;
  font-weight: 600;
  line-height: 32px;
  color: var(--black);
  margin-bottom: 8px;
  transition: 0.2s;
}

.dish-card:hover .dish-card__title {
  color: var(--primary-text-color);
}

.dish-card__subtitle {
  font-size: 16px;
  line-height: 24px;
  color: var(--gray);
  margin-bottom: 24px;
}

.dish-card__categories {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.dish-card__category {
  padding: 2px 14px;
  background-color: #f9f5ff;
  border-radius: 16px;
}

.dish-card__category-text {
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
  color: var(--primary-text-color);
}
