@media screen and (max-width: 720px) {
  .header__menu,
  .header__auth {
    display: none;
  }

  .header__hamburger-container {
    display: block;
  }

  .dishes__title {
    text-align: center;
  }
  .dishes__list {
    justify-content: center;
  }

  .dish-card {
    max-width: 343px;
  }

  .dish-card__image-container {
    height: 200px;
    margin-bottom: 24px;
  }

  .dish-card__title {
    font-size: 20px;
    line-height: 30px;
  }
}
